import React, { useEffect, useState } from "react";
import Header from "../../components/Header/Header";
import Footer from "../../components/Footer/Footer";
import { Select } from "antd";
import itemservice1 from "../../img/item_service1.png";
import itemservice2 from "../../img/item_service2.png";
import itemservice3 from "../../img/item_service3.png";
import itemservice4 from "../../img/item_service4.png";
import ReactImageMagnify from "react-image-magnify";
import * as cartAction from "../../action/cart.action";
import * as modalAction from "../../action/modal.action";
import "antd/dist/antd.css";
import "./details.css";
import Axios from "axios";
import { useDispatch } from "react-redux";
function DetailsPage(props) {
  const dispatch = useDispatch();
  const [valueInput, setValueInput] = useState(1);
  useEffect(() => {
    window.scrollTo(0, 0);
    const btnNextSmall = document.querySelector(".btn-next-small");
    const btnPreSmall = document.querySelector(".btn-pre-small");
    const listItemsSmall = document.querySelector(".img-small-center");
    function scroll() {
      let i = 0;
      btnNextSmall.addEventListener("click", () => {
        let width = document.querySelector(".img-small-item").clientWidth;
        i++;
        if (i > 2) {
          i = 0;
        }
        listItemsSmall.style.transform = "translateX(" + -i * width + "px)";
      });
      btnPreSmall.addEventListener("click", () => {
        let width = document.querySelector(".img-small-item").clientWidth;
        i--;
        if (i < 0) {
          i = 2;
        }
        listItemsSmall.style.transform = "translateX(" + -i * width + "px)";
      });
    }
    scroll();
    var imgArr = [...document.querySelectorAll(".img-small-item img")];
    imgArr.forEach((item) => {
      item.addEventListener("click", () => {
        imgArr.forEach((item) => {
          item.classList.remove("img-active");
        });
        item.classList.add("img-active");
      });
    });
  }, []);
  const { Option } = Select;
  const [showmore, setShowmore] = useState(false);
  const [item, setItem] = useState({});
  const id = props.match.params.id;
  useEffect(() => {
      Axios.get(`/product/${id}`)
        .then((data) => setItem(data.data[0]))
        .catch((err) => console.log(err));
  }, [id]);
  useEffect(() => {
    if (item.url) {
      setImg_big(item.url);
    }
  }, [item]);
  const [img_big, setImg_big] = useState("");
  const showimgsmall = () => {
    let result;
    var arr = [
      item.url,
      "https://images.pexels.com/photos/1236701/pexels-photo-1236701.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
      "https://images.pexels.com/photos/719396/pexels-photo-719396.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
      "https://images.pexels.com/photos/1591447/pexels-photo-1591447.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
      "https://images.pexels.com/photos/1366919/pexels-photo-1366919.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500",
    ];
    result = arr.map((item, index) => {
      return (
        <div key={index} className="img-small-item">
          <img onClick={() => setImg(item)} src={item} alt={index} />
        </div>
      );
    });
    return result;
  };
  const setImg = (img) => {
    setImg_big(img);
  };
  //POST data
  const [size, setSize] = useState(35);
  const [errAmount, setErrAmount] = useState("");
  const handleChange = (value) => {
    setSize(value);
  };
  const handleChangeInput = (e) => {
    setValueInput(e);
  };
  const incre = () => {
    setValueInput(Number(valueInput) + 1);
  };
  const decre = () => {
    if (valueInput <= 1) {
      setValueInput(1);
    } else {
      setValueInput(Number(valueInput) - 1);
    }
  };
  //Mua hang
  const buyNow = () => {
    if (valueInput < 1) {
      setErrAmount("S lng phi ln hn 1");
    } else {
      dispatch(
        modalAction.showModal1(id, item.url, item.name, item.price, item.type)
      );
      dispatch(
        cartAction.addToCart(
          id,
          item.url,
          item.name,
          item.price,
          Number(valueInput),
          size,
          item.type
        )
      );
      dispatch(modalAction.showModal2());
      setErrAmount("");
    }
  };
  return (
    <div>
      <Header />
      <div className="details_page">
        <div className="details-title">
          <div className="container">
            Trang ch {">"} <span>{item.name}</span>
          </div>
        </div>
        <div className="container details_middle">
          <div className="details-product">
            {/* img */}
            <div className="details-img">
              <div className="img-big">
                <ReactImageMagnify
                  {...{
                    smallImage: {
                      alt: "Wristwatch by Ted Baker London",
                      isFluidWidth: true,
                      src: img_big,
                    },
                    largeImage: {
                      src: img_big,
                      width: 700,
                      height: 700,
                      enlargedImageContainerDimensions: {
                        width: 200,
                        height: 100,
                      },
                      enlargedImageContainerClassName: "img-big",
                    },
                    isHintEnabled: true,
                    shouldHideHintAfterFirstActivation: false,
                    enlargedImagePosition: "beside",
                  }}
                />
              </div>
              <div className="img-small">
                <div className="btn-pre btn-pre-small">
                  <i className="fas fa-chevron-left"></i>
                </div>
                <div className="img-small-mid">
                  <div className="img-small-center">{showimgsmall()}</div>
                </div>
                <div className="btn-next btn-next-small">
                  <i className="fas fa-chevron-right"></i>
                </div>
              </div>
            </div>

            {/* end img */}

            {/* reviews */}
            <div className="reviews-product">
              <p className="reviewsname">{item.name}</p>
              <div className="star">
                <i className="far fa-star"></i>
                <i className="far fa-star"></i>
                <i className="far fa-star"></i>
                <i className="far fa-star"></i>
                <i className="far fa-star"></i>
              </div>
              <p className="rev-price">
                {item.price ? item.price.toLocaleString() : ""}
              </p>
              <div className="more-product">
                c k tha nhng chi tit ca nhng i Chuck 70 nh m{" "}
                <span>{item.name}</span> cng c lm t cht liu vi Canvas
                nhng k thut dt hon ho khin mt giy p nh mt bc tranh
                va bt mt va to ra s m st cho i chn ngi dng.
              </div>
              <div className="promotion">
                <div className="promotion-item">
                  <i className="fas fa-check-circle"></i>
                  <span>
                    Tng thm Tote bag + Freeship vi n hng t 3.000.000
                  </span>
                </div>
                <div className="promotion-item">
                  <i className="fas fa-check-circle"></i>
                  <span>
                    Tng thm Tote bag + Freeship vi n hng t 3.000.000
                  </span>
                </div>
                <div className="promotion-item">
                  <i className="fas fa-check-circle"></i>
                  <span>
                    Tng thm Tote bag + Freeship vi n hng t 3.000.000
                  </span>
                </div>
                <div className="promotion-item">
                  <i className="fas fa-check-circle"></i>
                  <span>
                    Tng thm Tote bag + Freeship vi n hng t 3.000.000
                  </span>
                </div>
              </div>
              {item.type !== "accessories" ? (
                <div className="size">
                  <span style={{ fontSize: "1.1rem", marginRight: "0.9rem" }}>
                    Kch thc
                  </span>
                  <Select
                    defaultValue="35"
                    style={{ width: 150, textAlign: "center" }}
                    onChange={(e) => handleChange(e)}
                  >
                    <Option value="35">Size : 35</Option>
                    <Option value="36">Size : 36</Option>
                    <Option value="36.5">Size : 36.5</Option>
                    <Option value="37">Size : 37</Option>
                    <Option value="38">Size : 38</Option>
                    <Option value="39">Size : 39</Option>
                    <Option value="39.5">Size : 39.5</Option>
                  </Select>
                </div>
              ) : (
                ""
              )}
              <div className="amount">
                <p
                  style={{
                    fontSize: "1rem",
                    marginBottom: "0.5rem",
                    marginTop: "1rem",
                  }}
                >
                  S lng :
                </p>
                <button className="decre" onClick={decre}>
                  <i className="fas fa-minus"></i>
                </button>
                <input
                  type="number"
                  className="number"
                  value={valueInput}
                  onChange={(e) => handleChangeInput(e.target.value)}
                />
                <button type="button" className="incre" onClick={incre}>
                  <i className="fas fa-plus"></i>
                </button>
              </div>
              <p style={{ color: "red" }}>{errAmount}</p>
              <div className="buy">
                <button className="btn-buynow" onClick={buyNow}>
                  <p>mua ngay</p>
                  <span>Giao hng COD tn ni</span>
                </button>
                <button className="btn-call">
                  <p>Gi t hng</p>
                  <span>Vui lng gi 012345678</span>
                </button>
              </div>
            </div>
            {/* End reviews */}
            <div className="description">
              <p className="tab_title">m t sn phm</p>
              <div className={showmore ? "tab_content active" : "tab_content"}>
                <p>
                  <strong>thng tin sn phm</strong>
                </p>
                <table className="tab_table">
                  <thead></thead>
                  <tbody>
                    <tr>
                      <td>Thng hiu</td>
                      <td>Converse</td>
                    </tr>
                    <tr>
                      <td>Xut x thng hiu</td>
                      <td>M</td>
                    </tr>
                    <tr>
                      <td>Sn xut ti</td>
                      <td>Vit Nam</td>
                    </tr>
                    <tr>
                      <td>Model</td>
                      <td>Converse Chuck 70</td>
                    </tr>
                    <tr>
                      <td>Cht liu</td>
                      <td>Vi canvas - Leather</td>
                    </tr>
                    <tr>
                      <td>Hng dn bo qun</td>
                      <td>
                        <ul>
                          <li>
                            Trnh mang sn phm khi tri ma hoc thi tit xu
                             chng khng b t dn n bong trc.
                          </li>
                          <li>
                            Ct gi sn phm  ni thong mt  gi gn cht
                            lng ca sn phm  mc tt nht.
                          </li>
                          <li>Lau chi sn phm thng xuyn  trnh bi.</li>
                        </ul>
                      </td>
                    </tr>
                    <tr>
                      <td>Ch  bo hnh</td>
                      <td>Bo hnh chnh hng 1 thng theo phiu bo hnh</td>
                    </tr>
                    <tr>
                      <td>Quy trnh ng gi</td>
                      <td>
                        <ul>
                          <li>Giy</li>
                          <li>Hp giy</li>
                          <li>Ti ng Converse</li>
                          <li>Phiu bo hnh chnh hng</li>
                        </ul>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <p>
                  <strong>M t sn phm</strong>
                </p>
                <p className="description-name">
                  <span>{item.name} </span> k tha c nhng c im ni bt
                </p>
                <p>
                  c k tha nhng chi tit ca nhng i{" "}
                  <span>{item.type}</span>70 nh m, ln ny{" "}
                  <span>{item.category}</span> li tip tc tung ra mt phin
                  bn ngt ngo dnh cho cc c nng hin i, yu thch s ngt
                  ngo m khng qu l, thch ni bt m vn phi th hin c
                  s tinh t.
                </p>
                <p>
                  <span>Chuck 70 Popped Colour</span> cng c lm t cht liu
                  vi Canvas nhng k thut dt hon ho khin mt giy p nh
                  mt bc tranh va bt mt va to ra s m st cho i chn
                  ngi dng.{" "}
                </p>
                <img
                  src="https://bizweb.dktcdn.net/thumb/1024x1024/100/347/923/products/568800c-3.jpg?v=1599930418920"
                  alt=""
                />
                <p>
                  Ngoi ra, phn  giy mu trng ng thi thng va tr trung
                  va mang li mt v vintage, bn ngoi sn phm c ph sn
                  bng  giy c c v p ng  v sang chnh hn.  phin
                  bn Popped Colour Chuck 70 va c ra mt, phn  ngoi yu
                  t k tha c truyn thng cn c nhng chi tit mi nh
                  phn  c chia thnh cc layer khc nhau, va to im nhn
                  va to ra s khc bit cho mt dng sn phm mi. Phn 
                  giy  v tr gt cn c m thm mt lp cao su ln trn 
                  giy to hiu ng chng th v v bt mt.{" "}
                </p>
                <div className={showmore ? "show_more active" : "show_more"}>
                  <span onClick={() => setShowmore(true)} className="more-text">
                    Xem y 
                  </span>
                  <span
                    onClick={() => setShowmore(false)}
                    className="less-text"
                  >
                    Thu gn
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="item-service">
            <div className="wrap-item">
              <img src={itemservice1} alt="" />
              <div className="content_service">
                <p>Giao hng siu tc</p>
                <span>Nhn hng trong 24-72h</span>
              </div>
            </div>
            <div className="wrap-item">
              <img src={itemservice2} alt="" />
              <div className="content_service">
                <p>Giao hng siu tc</p>
                <span>Nhn hng trong 24-72h</span>
              </div>
            </div>
            <div className="wrap-item">
              <img src={itemservice3} alt="" />
              <div className="content_service">
                <p>Giao hng siu tc</p>
                <span>Nhn hng trong 24-72h</span>
              </div>
            </div>
            <div className="wrap-item">
              <img src={itemservice4} alt="" />
              <div className="content_service">
                <p>Giao hng siu tc</p>
                <span>Nhn hng trong 24-72h</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default DetailsPage;
