import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import * as cartAction from "../../action/cart.action";
import "./cart.css";
function Cart() {
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();
  const { cartList } = cart;
  const delCart = (id) => {
    dispatch(cartAction.deleteCart(id));
  };
  const showList = () => {
    let result = cartList.map((item, index) => {
      return (
        <tr key={index}>
          <td>
            <img src={item.url} alt="" />
          </td>
          <td>
            <p>{item.name}</p>
            {item.type === "accessories" ? (
              ""
            ) : (
              <span>Size : {item.size}</span>
            )}
            <br />
            <span className="block">{item.price.toLocaleString()}</span>
            <div className="del_btn">
              <i
                style={{ cursor: "pointer" }}
                className="fas fa-times"
                onClick={() => delCart(item.id)}
              ></i>
            </div>
          </td>
          <td className="none">{item.price.toLocaleString()}</td>
          <td className="btn_amount">
            <button
              onClick={() => {
                dispatch(cartAction.decreAmount(item.id));
              }}
            >
              -
            </button>
            <input type="number" disabled={true} value={item.amount} />
            <button
              onClick={() => {
                dispatch(cartAction.increAmount(item.id));
              }}
            >
              +
            </button>
          </td>
          <td className="none">
            {(item.price * item.amount).toLocaleString()}
          </td>
          <td className="none">
            <i
              style={{ cursor: "pointer" }}
              className="fas fa-times"
              onClick={() => delCart(item.id)}
            ></i>
          </td>
        </tr>
      );
    });
    return result;
  };
  return (
    <React.Fragment>
      <div className="cart_title">
        <div className="container">
          <p style={{ lineHeight: "3rem", margin: "0", fontSize: "1.1rem" }}>
            Trang ch {`>`} Gi hng
          </p>
        </div>
      </div>
      <div className="cart_page container">
        <h3>Gi hng</h3>
        {cartList.length >=1 ? (
          <table className="cart_table tab_table">
            <thead>
              <tr>
                <td>nh sn phm</td>
                <td>Tn sn phm</td>
                <td className="none">n gi</td>
                <td>S lng</td>
                <td className="none">Thnh tin</td>
                <td className="none">Xa</td>
              </tr>
            </thead>
            <tbody>{showList()}</tbody>
          </table>
        ) : (
          <p>Gi hng trng</p>
        )}
        <p className="total">
          <span style={{ fontSize: "1.2rem", fontWeight: "bold" }}>
            Tng tin :{cart.total.toLocaleString()}
          </span>
          <br />
          <button className="continue"><Link className="button_cart" to="/">tip tc mua hng</Link></button>
          <button className="checkout"><Link className="button_cart" to="/checkout">Thc hin thanh ton</Link></button>
        </p>
      </div>
    </React.Fragment>
  );
}

export default Cart;
